:host {
  display: block;
}

a {
  color: black
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background: #fff;
  border-bottom: 1px solid #e4e4e4;
  font-family: sans-serif;
}

.logo {
  font-size: 1.5em;
}

.logo img {
  object-fit: contain;
}

nav {
  display: flex;
  gap: 20px;
}

nav a {
  text-decoration: none;
}

.right-section {
  display: flex;
  gap: 20px;
}

.menu-icon {
  display: none;
  cursor: pointer;
}

@media (max-width: 768px) {
  nav {
    display: none;
    flex-direction: column;
    gap: 10px;
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    background: #333;
    padding: 10px 20px;
  }
  nav.open {
    display: flex;
  }
  .menu-icon {
    display: block;
  }
}
